<template>
  <div class="page">
    <div class="t-gray t-sm" style='padding:.5rem;'>请确认您的订单信息，并按提示完成支付</div>
    <div class="order-info ui-list">
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0">订单内容</div>
          <div flex-box="1">购买{{product.name}}</div>
        </div>
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0">订单价格</div>
          <div flex-box="1">
            <span class="t-orange">{{price}}</span>元
          </div>
        </div>
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0"></div>
          <div flex-box="1">
            <mt-button plain size="small" @click.native="doRefreshPrice">刷新价格</mt-button>
          </div>
        </div>
    </div>

    <mt-navbar v-model="payType">
      <mt-tab-item id="wechat_jsapi">微信</mt-tab-item>
      <mt-tab-item id="alipay">支付宝</mt-tab-item>
    </mt-navbar>
    <div v-if="payType=='wechat_jsapi'" style='padding:1rem 2rem;background:#fff;'>
      <div class="mb-20 t-center">
        <!-- <div class="t-xs mb-10">请扫二维码进行支付</div>
        <img :src='qrcode' style='width:50vw;height:50vw;max-width:300px;max-height:300px;' />
        <div class="t-xs">*如遇到支付问题,请截屏此页面,从微信扫一扫界面,打开相册选择截屏图片进行支付</div> -->
        <!-- <div class="mb-20">或</div> -->
        <mt-button @click.native='payOrder("wechat")' size="large" type="primary">去支付</mt-button>
      </div>
    </div>
    <div v-if='payType=="alipay"' style='padding:1rem 2rem;background:#fff;'>
      <div class="mb-20 t-center">
      <div v-html='payResult.form'></div>
      <mt-button class="mb-10" @click.native='payOrder("alipay")' size="large" type="primary">去支付</mt-button>
      <!-- <div class='t-xs t-gray'>*如果您在微信内打开此页面,请从浏览器打开</div> -->
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import QRCode from "qrcode";
import storage from "@/utils/storage";
import { weixinAuth } from "@/utils/config";
import api from "@/utils/api";

let timer = 0;
export default {
  data() {
    return {
      isAlipayOrWechat: "",
      popupFail: false,
      popupSuccess: true,
      popupPaying: true,
      product: {},
      orderId: "",
      price: 0,
      productId: "",
      payResult: {},
      payType: "wechat_jsapi",
      options: [
        {
          label: "微信",
          value: "wechat_jsapi"
        },
        {
          label: "支付宝",
          value: "alipay"
        }
      ],
      qrcode: "",
      qrtext: ""
    };
  },
  // watch: {
  //   payType(val) {
  //     //this.getQRcode();
  //   }
  // },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo
    })
  },
  methods: {
    ...mapActions({
      getPayInfo: "getPayInfo",
      refreshPrice: "refreshPrice",
      _getPayResult: "getPayResult",
      pay: "pay",
      _getUnifiedOrder: "getUnifiedOrder",
      _getUserInfo: "getUserInfo"
      //umsH5: "umsH5"
    }),
    doRefreshPrice() {
      this.refreshPrice(this.orderId).then(({ code, data, msg }) => {
        this.$toast(msg);
        if (code == 200) {
          this.price = data;
          //this.getQRcode();
        }
      });
    },
    getOrder() {
      api.getOrder({ product_id: this.productId }).then(data => {
        if (data.code == 202) {
          return this.$messagebox.alert("您已经购买该商品").then(() => {
            this.$router.push("/male");
          });
        }
        this.orderId = data.token;
      });
    },
    payOrder(type) {
      if (type == "wechat") {
        if (this.$tools.isWechat()) {
          api
            .payOrder({
              orderId: this.orderId,
              channel: "wechat_jsapi"
            })
            .then(data => {
              this.weixinPay(data.config, () => {
                return this.$messagebox.alert("购买成功").then(() => {
                  this.$router.push("/male");
                });
              });
            });
        } else {
          api
            .payOrder({
              orderId: this.orderId,
              channel: "wechat_h5"
            })
            .then(res => {
              if (res.code == 200) {
                window.location.href = res.mweb_url;
              }
            });
        }
      } else if (type == "alipay") {
        api
          .pay({
            product_id: this.productId,
            channel: "alipay"
          })
          .then(data => {
            console.log(data);
            this.payResult = data;
            this.$nextTick(() => {
              document.forms["alipaysubmit"].submit();
            });
          });
      }
    },
    getPayResult(token) {
      this._getPayResult({
        token
      }).then(({ code, msg }) => {
        if (code != 200) {
          clearTimeout(timer);
          timer = setTimeout(() => {
            this.getPayResult(token);
          }, 5000);
          return false;
        }
        return this.$messagebox.alert("支付完成,前往查看").then(() => {
          this.$router.push("/courseList");
        });
      });
    },
    jumpAliPage() {
      //console.log(document.getElementById("alipaysubmit"));
      document.getElementById("alipaysubmit").target = "_blank";
      document.forms["alipaysubmit"].submit();
    },
    bindOpenId(isGet) {
      if (this.$tools.isWechat()) {
        if (isGet <= 0) {
          //console.log(1111111);
          let { token } = storage.get("userToken");
          let redirect_uri = encodeURIComponent(
            window.location.href + `/${+isGet + 1}`
          );
          let href = `${weixinAuth}/api/weixinauth?token=${token}&url=${redirect_uri}`;
          window.location.href = href;
        }
      }
    }
    // jumpWechatPay() {
    //   this.getPay(data => {
    //     this.weixinPay(data.config, () => {
    //       return this.$messagebox.alert("购买成功").then(() => {
    //         this.$router.back();
    //       });
    //     });
    //   });
    // }
  },
  mounted() {
    let { id: product_id, isGet = 0 } = this.$route.params;
    //console.log(isGet);
    this.productId = product_id;
    this.getPayInfo({ product_id }).then(
      ({ code, msg, price, token, product }) => {
        if (code == 200) {
          this.price = price;
          // this.orderId = token;
          this.product = product;
          this.bindOpenId(isGet);
        }
      }
    );

    product_id && this.getOrder();
  },
  beforeDestroy() {
    clearTimeout(timer);
  }
};
</script>
<style lang="less" scoped>
.page {
  padding: 40px 0 50px;
}
.order-info {
  background: #fff;
  padding: 0.5rem;
}

.ui-list-item {
  margin-bottom: 0.5rem;
  .ui-list-label {
    color: #666;
    width: 3.2rem;
  }
}
.btn-pay {
  line-height: 41px;
  text-decoration: none;
}
</style>


